.card-spotlight {
  position: relative;
  border-radius: 1.5rem;
  border: 1px solid #222;
  background-color: #111;
  padding: 2rem;
  overflow: hidden;
  --mouse-x: 50%;
  --mouse-y: 50%;
  --spotlight-color: rgba(139, 92, 246, 0.15);
  /* Ensure hardware acceleration */
  transform: translateZ(0);
  will-change: transform;
}

.card-spotlight::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at var(--mouse-x, 50%) var(--mouse-y, 50%), var(--spotlight-color, rgba(139, 92, 246, 0.15)), transparent 80%);
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
  /* Ensure hardware acceleration */
  transform: translateZ(0);
  will-change: opacity, background;
}

.card-spotlight:hover::before,
.card-spotlight:focus-within::before {
  opacity: 0.6;
}

/* Fallback for browsers that don't support CSS custom properties */
@supports not (background: radial-gradient(circle at var(--mouse-x) var(--mouse-y), var(--spotlight-color), transparent 80%)) {
  .card-spotlight::before {
    background: radial-gradient(circle at 50% 50%, rgba(139, 92, 246, 0.15), transparent 80%);
  }
}